.SplashPage {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: 100%;
  width: 100%;
  -webkit-perspective: 1px;
          perspective: 1px;
  background: 
    -webkit-gradient(
      linear, 
      left top, left bottom, 
      from(rgba(0, 0, 0, 0.05)), 
      to(rgba(0, 0, 0, 0.05))
    ),
  url('/background.jpg');
  background: 
    -webkit-linear-gradient(
      rgba(0, 0, 0, 0.05), 
      rgba(0, 0, 0, 0.05)
    ),
  url('/background.jpg');
  background: 
    -o-linear-gradient(
      rgba(0, 0, 0, 0.05), 
      rgba(0, 0, 0, 0.05)
    ),
  url('/background.jpg');
  background: 
    linear-gradient(
      rgba(0, 0, 0, 0.05), 
      rgba(0, 0, 0, 0.05)
    ),
  url('/background.jpg');  
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
}

.SplashPage .parallax {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
  position: relative;
  height: 100vh;
  -webkit-transform: translateZ(-1px) scale(2);
          transform: translateZ(-1px) scale(2);
  z-index: -1;
}

.SplashPage .letter {
  color: #ffffff;
  display: inline-block;
  font-size: 150px;
  font-weight: 300;
  text-transform: uppercase;
  margin: 10px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-transition: 0.2s all;
  -o-transition: 0.2s all;
  transition: 0.2s all;
}

.SplashPage .down {
  cursor: pointer;
  -webkit-transition: 0.2s all;
  -o-transition: 0.2s all;
  transition: 0.2s all;
  -webkit-transform: rotateX(180deg);
          transform: rotateX(180deg);
}

.title:after {
  content: '';
  position: relative;
  display: block;
  bottom: 0;
  border-bottom: 2px solid #ffffff;
  background: #ffffff;
  width: 0;
}

.title:hover .letter {
  margin: 0 30px;
}

.title:hover .down {
  -webkit-transform: rotateX(0deg);
          transform: rotateX(0deg);
}




